
.boxzz {
    /* flex-basis: calc(10% - 400px); */
    
    background-color: gray;
    color: white;
    padding: 5px;
    margin: 5px;
    display: inline-block;
    border-radius: 5px;
    height: 17px;
    width: 15px;
    margin: 1.5px;
    background-color: rgb(53, 188, 53);
    border: 1px solid #ddd;
    box-sizing: border-box;
    display: inline-flex; 
    justify-content: space-between;
    align-items: center;
    font-size: 6px;
    font-weight: bold;
    color: #333;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    padding: 7px;
}
